// @flow

import styled from 'styled-components/macro';
import {colors, spacing, page, font} from '../../config/styles';
import Meta, {StyledMeta} from '../blog/meta';

export const StyledTitle = styled.h1`
    margin-bottom: 0;
`;

export default styled.article`
    margin: auto;
    width: ${page.width};
    max-width: calc(100% - ${spacing.large});
    max-width: calc(100vw - ${spacing.large});
    padding: ${spacing.medium};
    font-weight: ${font.weights.light};

    ${StyledTitle} + ${StyledMeta} {
        margin-bottom: ${spacing.large};
    }

    > * {
        display: block;
        ${page.post.withVar('width')};
        max-width: 100%;
        margin-left: auto;
        margin-right: auto;
    }

    ul, ol {
        list-style: none;
        padding: 0;

        li {
            position: relative;
            padding-left: 1.5rem;

            &::before {
                position: absolute;
                line-height: 1;
                padding-top: 0.25rem;
                top: 0;
                left: 0;
                bottom: 0;
                width: 1.5rem;
                font-size: 1.4em;
                content: '\\2022';
                ${colors.withVar('dimgray', 'color')};
            }
        }
    }

    ol {
        counter-reset: ol-li-counter;

        li::before {
            padding-top: 0.4rem;
            font-size: 0.9em;
            font-weight: ${font.weights.bold};
            counter-increment: ol-li-counter;
            content: counter(ol-li-counter)'.';
        }
    }

    img {
        max-width: 100%;
    }

    pre {
        padding: ${spacing.medium};
    }

    blockquote {
        font-style: italic;
        margin-top: ${spacing.large};
        margin-bottom: ${spacing.large};
        padding: 0 ${spacing.large};
        ${colors.fmtVar('dimgray', '4px solid ??', 'border-left')};

        p {
            opacity: 0.8;

            &:first-child {
                margin-top: 0;
        }

            &:last-child {
                margin-bottom: 0;
            }
        }

        @media all and (max-width: 480px) {
            padding: ${spacing.large} 0;
            border: none;
            ${colors.fmtVar('dimgray', '4px solid ??', 'border-top')};
            ${colors.fmtVar('dimgray', '4px solid ??', 'border-bottom')};
        }
    }

    > p {
        margin-top: ${spacing.large};
        margin-bottom: ${spacing.large};
    }

    h1, h2, h3, h4, h5, h6 {
        &:not(:first-child) {
            margin-top: ${spacing.large};
        }
    }

    h1 + p, h2 + p, h3 + p, h4 + p, h5 + p, h6 + p {
        margin-top: ${spacing.regular};
    }
`;

export function Title({author, title, date, words}: PostMeta) {
    return (
        <>
            <StyledTitle>{title}</StyledTitle>
            <Meta {...{author, date, words}} />
        </>
    );
}

export const StyledFooter = styled.footer`
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
`;
